import preval from "preval.macro";

type BlogPage = {
    id: string;
    title: string;
    author: string;
    date?: Date;
    tags: string[];
    isPinned: boolean;
    content: string;
};

const rawPages: any[] = preval`
    module.exports=require(__dirname +'/pageDataFetch.js');
`;

const pages: BlogPage[] = [];

for (const rawPage of rawPages) {
    const page: BlogPage = {
        id: rawPage.id,
        title: rawPage.title,
        author: rawPage.author || "Gareth Fultz",
        date: rawPage.date !== undefined ? new Date(rawPage.date) : undefined,
        tags: rawPage.tags || [],
        isPinned: rawPage.isPinned || false,
        content: rawPage.content,
    };

    if (page.isPinned) {
        pages.unshift(page);
    } else {
        pages.push(page);
    }
}

export default pages;
